                            <view class="info-item">
                                <text class="label">‰ªäÊó•Êî∂ËøêÔºö</text>
                                <text class="value">{{ item.estimateBucketNum ?? 0 }} Ê°∂</text>
                            </view>
                            <view class="info-item">
